.home-background{
    background: url(https://res.cloudinary.com/dz6oeh4ym/image/upload/v1619882372/kiranaddagarla-primary-background.jpg);
    height: 100%;
    width: 100%;
    background-size:100% 100%;
    background-repeat:no-repeat;
    overflow-y: hidden;
    overflow-x: hidden;
 }

.home-background-overlay {
    position: absolute;
    min-height: 100%;
    min-width: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.6);
}

.profile-image{
    border-radius: 20%;
    height: 200px;
    width: 200px;
}
.primary-section-container{
    height: 100vh;
    width: 100%;
}
.secondary-section-container{
    height: 100vh;
    width: 100%;
    background-color: rgb(213, 225, 226);
}

.navigation-bar:hover,:active{
    background-color: whitesmoke;
    color: black;
}

.navigation-bar-logo:hover{
    background-color: black;
}